
import {
  WebGLRenderer,
  PerspectiveCamera,
  Scene,
  Vector3,
  Clock,
  Color,
  sRGBEncoding,
  AnimationMixer,
  TextureLoader,
  RepeatWrapping,
  NearestFilter,
  PlaneGeometry,
  MeshLambertMaterial,
  DoubleSide,
  Mesh,
  AmbientLight,
  HemisphereLight,
  DirectionalLight,
  MathUtils,
  Box3,
  Box3Helper,
  CatmullRomCurve3,
  BufferGeometry,
  LineBasicMaterial,
  Line,
  Object3D
  } from 'three';

import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { GUI } from 'three/examples/jsm/libs/dat.gui.module.js';

import GroundTexture from './objects/grasslight-big.jpg';
import MalliGlb from './objects/Villisika/Willisika.glb';
import TreeGlb from './objects/Tree/Tree-scene.glb';

function main() {

  // camera
  const fov = 50;   // Default is 50
  const aspect = 2;  // the canvas default
  const near = 0.1;
  const far = 100;
  const camera = new PerspectiveCamera(fov, aspect, near, far);
  camera.position.set(0, 10, 20);

  const renderer = new WebGLRenderer({
    antialias: true,
    outputEncoding: sRGBEncoding
   });
   renderer.setPixelRatio(window.devicePixelRatio);
   renderer.shadowMap.enabled = true;

   const leveys = window.innerWidth;
   const korkeus = window.innerHeight;

   // Size of renderer should be the same as the window
   renderer.setSize( leveys, korkeus );

   const canvas = renderer.domElement;

  // Add an orbit control which allows us to move around the scene.
  const controls = new OrbitControls( camera, canvas );
        controls.target.set( 0, 5, 0 );
  			controls.update();

  const scene = new Scene();
  scene.background = new Color('#DEFEFF');

  {
    // ground
    const PlaneSize = 250;
    const loader = new TextureLoader();

		GroundTexture = loader.load( GroundTexture );
		GroundTexture.wrapS = GroundTexture.wrapT = RepeatWrapping;
		GroundTexture.repeat.set( 25, 25 );
    GroundTexture.magFilter = NearestFilter;
		GroundTexture.anisotropy = 16;
		GroundTexture.encoding = sRGBEncoding;

		const GroundMaterial = new MeshLambertMaterial( {
      map: GroundTexture,
      side: DoubleSide
     } );

    const PlaneGeo = new PlaneGeometry(PlaneSize, PlaneSize);

    const mesh = new Mesh(PlaneGeo, GroundMaterial);
    mesh.position.y = - 10;
    mesh.receiveShadow = true;
    mesh.rotation.x = Math.PI * -0.5;
    scene.add(mesh);
  }

  // lights

  {
    const skyColor = 'lightblue';
    const groundColor = 'lightgreen';
    const intensity = 1;
    const light = new HemisphereLight(skyColor, groundColor, intensity);
    scene.add(light);
  }

  {
    const color = 0xFFFFFF;
    const intensity = 1;
    const light = new DirectionalLight(color, intensity);
    light.castShadow = true;
    light.position.set(-250, 800, -850);
    light.target.position.set(-550, 40, -450);

    light.shadow.bias = -0.004;
    light.shadow.mapSize.width = 1024;
    light.shadow.mapSize.height = 1024;

    const cam = light.shadow.camera;
    cam.near = 1;
    cam.far = 1000;
    cam.left = -1500;
    cam.right = 1500;
    cam.top = 1500;
    cam.bottom = -1500;

    scene.add(light);
    scene.add(light.target);
  }

  function frameArea(sizeToFitOnScreen, boxSize, boxCenter, camera) {
    const HalfSizeToFitOnScreen = sizeToFitOnScreen * 0.5;
    const halfFovY = MathUtils.degToRad(camera.fov * 0.5);
    const distance = HalfSizeToFitOnScreen / Math.tan(halfFovY);
    // compute a unit vector that points in the direction the camera is now
    // in the xz plane from the center of the box
    const direction = (new Vector3())
        .subVectors(camera.position, boxCenter)
        .multiply(new Vector3(1, 0, 1))
        .normalize();

    // move the camera to a position distance units way from the center
    // in whatever direction the camera was from the center already
    camera.position.copy(direction.multiplyScalar(distance).add(boxCenter));

    // pick some near and far values for the frustum that
    // will contain the box.
    camera.near = boxSize / 100;
    camera.far = boxSize * 100;

    camera.updateProjectionMatrix();

    // point the camera to look at the center of the box
    camera.lookAt(boxCenter.x, boxCenter.y, boxCenter.z);
  }

    let curve;
    let curveObject;

    {
      curve = new CatmullRomCurve3( [
        new Vector3( -30, 0, 20 ),
        new Vector3( -10, 0, 10 ),
  	    new Vector3( 0, 0, 0 ),
  	    new Vector3( -10, 0, -5 ),
  	    new Vector3( -20, 0, -10 )
        ] );
        curve.closed = true;
    }

   {
        const points = curve.getPoints( 250 );
        const geometry = new BufferGeometry().setFromPoints( points );
        const material = new LineBasicMaterial( { color : 0xff0000 } );

        // Create the final object to add to the scene
        curveObject = new Line( geometry, material );
        curveObject.scale.set(2, 2, 2 );
        material.depthTest = false;
        curveObject.visible = false;
//        curveObject.rotation.set( Math.PI * 0, Math.PI * 0, Math.PI * 0 );

        curveObject.position.x = 40;
        curveObject.position.y = -10;
        curveObject.renderOrder = 1;
        scene.add(curveObject);
    }

  let mixer;
  const animals = [];
  let speedFactor;

  // GUI parameters
  const params = {
    Juoksua: true,
    Uhkailua: false
      };

  {
    const GLTFloader = new GLTFLoader();

    GLTFloader.load(MalliGlb, (gltf)=>{
      const root = gltf.scene;
      const loadedAnimal = root.getObjectByName('RootNode');

      root.updateMatrixWorld();

      const obj = new Object3D();
      loadedAnimal.getWorldPosition(obj.position);
      loadedAnimal.position.set(0, 0, 0);
      loadedAnimal.rotation.set( Math.PI / 2, Math.PI, 2 * -Math.PI / 3 );
      obj.add(loadedAnimal);
      loadedAnimal.scale.set( 0.1, 0.1, 0.1 );
      scene.add( obj );
      animals.push( obj );

      loadedAnimal.traverse(( obj ) => {
        if (obj.castShadow !== undefined)
        {
          obj.castShadow = true;
          obj.receiveShadow = false;
        }
      });

//      console.log(dumpObject( loadedAnimal ).join('\n') );

      // compute the box that contains all the stuff
      // from loadedAnimal / root and below
      const box = new Box3().setFromObject( loadedAnimal );

      const boxSize = box.getSize(new Vector3()).length();
      const boxCenter = box.getCenter(new Vector3());

      // set the camera to frame the box
      frameArea( 1.5 * boxSize, boxSize, boxCenter, camera );

      // update the controls to handle the new size
      controls.maxDistance = boxSize * 10;
      controls.target.copy(boxCenter);
//      controls.autoRotate = true;
//      controls.update();


     const gui = new GUI( {
       width: 200,
       autoPlace: true
       } );

      mixer = new AnimationMixer( obj );
      mixer.clipAction( gltf.animations[ 0 ] ).play();   // BATTLE_RUN-Take 001
      speedFactor = 1.0;

      gui.add( params, 'Juoksua' )
       .listen()
       .onChange( function () {
         if ( params.Juoksua == !true ) {
           mixer.clipAction( gltf.animations[ 0 ] ).stop();   // BATTLE_RUN-Take 001
           mixer.clipAction( gltf.animations[ 20 ] ).stop(); // SPECIAL_PAWN-Take 001
           mixer.clipAction( gltf.animations[ 19 ] ).play(); // NORMAL_WALK-Take 001
           speedFactor = 0.1;
         } else {
           mixer.clipAction( gltf.animations[ 19 ] ).stop();   // NORMAL_WALK-Take 001
           mixer.clipAction( gltf.animations[ 20 ] ).stop(); // SPECIAL_PAWN-Take 001
           mixer.clipAction( gltf.animations[ 0 ] ).play();   // BATTLE_RUN-Take 001
           speedFactor = 1.0;
         }

       } );
       gui.add( params, 'Uhkailua' )
        .listen()
        .onChange( function () {
          if ( params.Uhkailua == true ) {
            mixer.clipAction( gltf.animations[ 0 ] ).stop();   // BATTLE_RUN-Take 001
            mixer.clipAction( gltf.animations[ 19 ] ).stop(); // NORMAL_WALK-Take 001
            mixer.clipAction( gltf.animations[ 20 ] ).play(); // SPECIAL_PAWN-Take 001
            params.Juoksua = false;
            speedFactor = 0.1;
          } else {
            mixer.clipAction( gltf.animations[ 20 ] ).stop(); // SPECIAL_PAWN-Take 001
            mixer.clipAction( gltf.animations[ 19 ] ).stop();   // NORMAL_WALK-Take 001
            mixer.clipAction( gltf.animations[ 0 ] ).play();   // BATTLE_RUN-Take 001
            params.Juoksua = true;
            speedFactor = 1.0;
          }

        } );

    })
  }

  {
    const TreeGLTFloader = new GLTFLoader();

      TreeGLTFloader.load(TreeGlb, (gltf)=>{
        const model = gltf.scene;
        model.scale.set( 0.03, 0.03, 0.03 );
        model.position.set(-25, -10, 10);
        scene.add( model );

        let model2 = model.clone();
        model2.scale.set( 0.03, 0.03, 0.03 );
        model2.position.set(0, -10, -10);
        scene.add( model2 );

        let model3 = model.clone();
        model3.scale.set( 0.02, 0.02, 0.02 );
        model3.position.set(15, -10, -5);
        scene.add( model3 );
      } )
  }

  function resizeRendererToDisplaySize(renderer) {
    const canvas = renderer.domElement;
    const width = canvas.clientWidth;
    const height = canvas.clientHeight;
    const needResize = canvas.width !== width || canvas.height !== height;
    if (needResize) {
      renderer.setSize(width, height, false);
    }
    return needResize;
  }

  const clock = new Clock();

  // create 2 Vector3s we can use for path calculations
  const animalPosition = new Vector3();
  const animalTarget = new Vector3();

    function render(time) {
      time *= 0.001; //convert to seconds

    if (resizeRendererToDisplaySize(renderer)) {
      const canvas = renderer.domElement;
      camera.aspect = canvas.clientWidth / canvas.clientHeight;
      camera.updateProjectionMatrix();
    }

    {
      const pathTime = speedFactor * time * 0.1 ;
      const targetOffset = 0.15;
      animals.forEach((animal, idx) => {
        // a number between 0 and 1 to evenly space the animals
        const u = pathTime + idx / animals.length;

        // get the first point
        curve.getPointAt(u % 1, animalPosition);
        animalPosition.applyMatrix4(curveObject.matrixWorld);

        // get a second point slightly further down the curve
        curve.getPointAt((u + targetOffset) % 1, animalTarget);
        animalTarget.applyMatrix4(curveObject.matrixWorld);

        // put the animal at the first point (temporarily)
        animal.position.copy(animalPosition);
        // point the animal the second point
        animal.lookAt(animalTarget);

        // put the animal between the 2 points
        animal.position.lerpVectors(animalPosition, animalTarget, 0.5);

      });
    }

    {
    const delta = clock.getDelta();
    if (mixer) {
      mixer.update( delta );
      controls.update();
      }
    }

    renderer.render(scene, camera);
    requestAnimationFrame(render);
  }

  requestAnimationFrame(render);

// resize
const windowResizeHandler = () => {
  const { innerHeight, innerWidth } = window;
  renderer.setSize(innerWidth, innerHeight);
  camera.aspect = innerWidth / innerHeight;
  camera.updateProjectionMatrix();
};
windowResizeHandler();
window.addEventListener('resize', windowResizeHandler);

// dom
document.body.style.margin = 0;
document.body.appendChild( renderer.domElement );

// Helper func() to show object contents
function dumpObject(obj, lines = [], isLast = true, prefix = '') {
  const localPrefix = isLast ? '' : '';
  lines.push(`${prefix}${prefix ? localPrefix : ''}${obj.name || '*no-name*'} [${obj.type}]`);
  const newPrefix = prefix + (isLast ? '  ' : ' ');
  const lastNdx = obj.children.length - 1;
  obj.children.forEach((child, ndx) => {
    const isLast = ndx === lastNdx;
    dumpObject(child, lines, isLast, newPrefix);
  });
  return lines;
}

}

main();
